.discount-containerMain{
    display: flex;
    padding-top: 50px;
    justify-content: space-evenly;
    text-align: left;
    margin-bottom: 50px;
    padding-bottom: 50px;
    background: rgb(227, 227, 227);
}
.discount-left{
    width: 500px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: left;
}
.discount-left>h2{
    color: var(--clr-primary-5);
    font-size: 3.7rem;
}
.discount-left>h2>span{
    font-size: 17px;
    color: black;
}
.discount-left p{
    color: var(--clr-black);
    width: 410px;
    line-height: 23px;
}

.discount-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 45px;
    border-radius: 50px;
    padding: 0.7rem;
    background: var(--clr-primary-5);
    border: none;
    color: var(--clr-white);
    font-size: 17px;
}
.discount-btn:hover{
    background: var(--clr-white);
    transition: var(--transition);
    color: var(--clr-black);
    border: 2px solid var(--clr-primary-5);
}
.discount-right img{
    height: 250px;
    width: 30rem;
}