.card{
    margin: 20px;
    padding: 20px;
    cursor: pointer;
    width: 16rem;
}
.card-link{
    color: inherit;
}
.card-img{
    width: 13rem;
    height: 7rem;
    margin-bottom: 1rem;
}
.card-title{
    font-size: 20px;
}
.card-reviews{
    margin-bottom: 0.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.rating-star{
    color: #d5ab55;
}
.total-reviews{
    font-size: 0.9rem;
    margin-left: 10px;
}
.card-price{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.bag-icon{
    color: #535353;
}
.bag-icon:hover{
    color: red;
}
