.MuiTableRow-root>*{
    padding: 10px;
}
.MuiTableRow-root.MuiTableRow-head>*{
    font-weight: bold !important;
}
.Table:first-child{
    border-radius: 0.7rem !important;

}

.status{
    padding: 8px;
    border-radius: 9px;
}
.tableDetail{
    color: blue !important;
    cursor: pointer;
}
@media screen and (max-width:1200px){
    .Table{
        width: 170%;
        margin-top: 6rem;

    }
}
@media screen and (max-width:750px){
    .Table{
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 21rem;
    }
}