.RightSide{
    display: flex;
    flex-direction: column;
    width: 95%;
    justify-content: space-evenly;
}
@media screen and (max-width:1200px){
    .RightSide{
        justify-content: flex-start;
        margin-top: 2rem;
    }
    .RightSide>div>h3{
        
        font-size: 20px;
    }
}
@media screen and (max-width:750px){
    .RightSide{
        width: 100%;
        margin-top: 0;
        gap: 20px;
    }
    .RightSide>div{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}