.containerFooter{
    background: var(--clr-black);
    overflow: hidden;
    
}
.FooterBottom{
    display: flex;
    justify-content: center;
    text-align: center;
    padding-top: 10px;
    color: white;
}
.footerData{
    display: flex;
    text-align: left;
    justify-content: space-evenly;
}
.footerLinks{
    display: flex;
    flex-direction: column;
    padding: 40px;
    gap: 20px;
    color: white;
}
.FooterBottom>p, .footerLinks>div>p{
    color: white;
}
.f_a{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.footerLinks a{
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: white;
    font-weight: 500;
}
.footerLinks a:hover{
    color: rgb(17,231,242);
    text-decoration: underline;
}