.tryon-container{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.tryon-images{
    display: grid;
    grid-template-columns: auto auto;
    gap: 10rem;
}
.glass-image{
    height: 25rem;
    width: 35rem;
}
.customer-image{
    height: 25rem;
    width: 35rem;
}