.Updates{
    width: 85%;
    background: white;
    border-radius: 0.7rem;
    padding: 1rem;
    gap: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 13px;
    margin-bottom: 1rem;
    font-weight: 400;

}
.update{
    display: flex;
    gap: 0.5rem;
}

.update>img{
    width: 4.5rem;
    height: 4.2rem;
    border-radius: 50%;
    margin: auto;
}
@media screen and (max-width:1200px){
    .Updates{
        width: 21.5rem;
        margin-top: 1.5rem;
    }
    .Updates>h3{
        margin-top: 1rem;
    }
}