/* EmailForm.css */

.mailContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.emailForm {
  background: transparent;
  max-width: 600px;
  width: 100%;
  display: grid;
  grid-template-columns: 40% 45%;
  gap: 20px;
}
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
    width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}
