.Sidebar{
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 4rem;
    transition: all 300ms ease;
    
}
.sidebarLogo{
    display: flex;
    height: 5rem;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    height: 4%;

}
.sidebarLogo img{
    width: 6rem;
    height: 4rem;
}
.sidebarLogo>span>span{
    color: var(--clr-primary-5);
}

.menu{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.menuItem{
    display: flex;
    align-items: center;
    gap: 1rem;
    height: 2.5rem;
    position: relative;
    margin-left: 2.5rem;
    transition: all 300ms ease;
    font-size: 14px;
    border-radius: 0.7rem;
}
.menuItem:hover{
    cursor: pointer;
}
.active{
    background: var(--activeItem);
    margin-left: 0;
}
.active::before{
    content: "";
    width: 8px;
    height: 100%;
    background: var(--clr-primary-5);
    margin-right: calc(1rem-8px);
}
.menu .menuItem:last-child{
    position: absolute;
    bottom: 2.3rem;
    width: 100%;
}
.bars{
    display: none;
}
@media screen and (max-width:1200px){
    .menuItem>span{
        display: none;
    }
    .sidebarLogo{
        display: none;
    }
}
@media screen and (max-width:750px){
    .Sidebar{
        position: fixed;
        z-index: 9;
        background: var(--glass);
        width: 55%;
        padding-right: 1rem;
        height: 100%;
    }
    .menuItem>span{
        display: block;
    }
    .sidebarLogo{
        display: flex;
    }
    .menu .menuItem:last-child{
        position: relative;
        margin-top: 2rem;
    }
    .bars{
        display: flex;
        position: fixed;
        top: 2rem;
        z-index: 9;
        background: transparent;
        padding: 10px;
        border-radius: 10px;
    }
}