.page{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.singleContainer {
  position: relative;
  display: grid;
  grid-template-columns: 45% 35%;
  align-items: center;
  justify-content: space-evenly;
  justify-items: center;
}
.product-img > img {
  height: 100%;
  max-width: 100%;
  display: block;
  margin: auto;
  object-fit: contain;
}
.singleProductDetails {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 20px;
}
.stock {
  display: flex;
  align-items: baseline;
}
.product-buttons {
  display: grid;
  grid-template-columns: 20% 25%;
  align-items: center;
  gap: 15px;
}
.product-buttons > button, .tryon-btn>button {
  font-weight: bold;
  height: 35px;
  color: var(--clr-primary-5);
  border: 1px solid var(--clr-primary-5);
  background: transparent;
  padding: 5px 10px;
}
.product-buttons > button:hover, .tryon-btn>button:hover {
  border: 1px solid var(--clr-grey-8);
  background: var(--clr-primary-5);
  color: white;
}
.article-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3rem;
}
.article{
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 10rem;
    row-gap: 3rem;
}


