.containerMain{
    display: flex;
    padding-top: 60px;
    justify-content: space-around;
    height: 100vh;
    text-align: left;
    padding-bottom: 100px;
    background-image: url('./assets/images/main.jpg');
    background-size: cover;
}
.left{
    margin-top: 2rem;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: left;
}
.left>h2{
    color: var(--clr-primary-5);
    font-size: 3.7rem;
}
.left p{
    width: 410px;
    line-height: 23px;
    color: black;
}
.main-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 45px;
    border-radius: 50px;
    font-size: 17px;
    padding: 0.7rem;
    background: var(--clr-primary-5);
    border: none;
    color: var(--clr-white);
}
.main-btn:hover{
    background: var(--clr-white);
    transition: var(--transition);
    color: var(--clr-black);
    border: 3px solid var(--clr-primary-5);
}