
.containerKeyFeatures{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-top: 5rem;
}
.containerKeyFeatures>.heading>h2{
    font-size: 3.5rem;
}
.keyFeatures{
    display: flex;
    gap: 90px;
}
.objectFeatures{
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 10px;
    max-width: 300px;
    background: white;
    margin-bottom: 60px;
    margin-top: 60px;
    padding: 20px;
    border-radius: 10px;
    align-items: center;
}
.featuresIcons{
    height: 60px;
    width: 60px;
}
.featuresIcons:hover{
    color: rgb(17,231,242);
}