.section {
    padding: 5rem 15rem;
  }
  .section-title {
    font-size: 2rem;
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
    text-align: center;
    margin-bottom: 3.5rem;
    margin-top: 1rem;
  }
.about-section {
    width: var(--smallWidth);
    max-width: var(--maxWidth);
    margin: 0 auto;
  }
  .about-section p {
    line-height: 2rem;
    font-weight: 400;
    letter-spacing: 2px;
  }